import React from "react";
import "./Cards.css";
import CardItem from "./CardItem";

function Cards() {
  return (
    <div className="cards">
      <h1>Uma cidade rica em cultura!</h1>
      <div className="cards__container">
        <div className="cards__wrapper">
          <ul className="cards__items">
            <CardItem
              src="images/img-9.jpg"
              text="O Galo da Madrugada, maior bloco carnavalesco do mundo"
              label="Carnaval"
              path="/services"
            />
            <CardItem
              src="/images/img-2.jpg"
              text="Bolo de Rolo, melhor doce do Brasil"
              label="Gastronomia"
              path="/services"
            />
          </ul>
          <ul className="cards__items">
            <CardItem
              src="images/img-3.jpg"
              text="Praia de Boa Viagem"
              label="Lazer"
              path="/services"
            />
            <CardItem
              src="images/img-4.jpg"
              text="Parque das esculturas de Brennand"
              label="Obras de Arte"
              path="/products"
            />
            <CardItem
              src="images/img-8.jpg"
              text="Praia de Porto de Galinhas, uma das praias mais lindas do nosso litoral!"
              label="Lazer"
              path="/sign-up"
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
